<template>
    <div>
        <div id="log">
            <div id="back">
                <el-row id="login-text">
                    <el-col :span="24" ><div><h1 class="first-green">Login</h1></div></el-col>
                </el-row>
                <el-alert v-if="loginErrorMsg" title="Login falhou" description="Email ou senha incorretos" type="warning" show-icon></el-alert>  
                <div id="form">
                    <el-form ref="form" id="form" @click.prevent="tryConnection(loginURL)">
                        <el-row>
                            <el-col :span="24" class="content">
                                <el-form-item label="Email" id="emailLabel">
                                    <el-input v-model="email" name="email" :autofocus="true" auto-complete="true" type="email" @blur="saveEmail"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="24" class="content">
                                <el-form-item label="Senha" id="passwordLabel">
                                    <el-input v-model="password" name="password" type="password"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="24" class="content" id="buttons">
                                <el-form-item>
                                    <el-button type="success" size="large" @click="tryConnection(loginURL)">Entrar</el-button>
                                </el-form-item>
                                <el-form-item>
                                    <router-link to="/"><el-button :plain="true" type="danger" size="large">Cancelar</el-button></router-link>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                email: '',
                password: '',
                address: 'localhost:8000/',
                loginURL: '/api/login',
                em: document.getElementsByClassName('content'),
                loginErrorMsg: false
            }
        },
        methods: {
            fixSize(val) {
                for (let el of this.em) {
                    el.style.width = val + 'px';
                }
            },
            Sizes() {
                (this.windowWidth < 450) ? this.fixSize(250): this.fixSize(400);
            },
            tryConnection(url) {

                let form = new FormData();
                form.append('email', this.email);
                form.append('password', this.password);


                this.$http.post(url, form).then(response => {
                    const resposta = response.body;
                    if (resposta.hasOwnProperty('nivel')) {
                        this.verificarNivel(resposta.nivel);
                    } else if (resposta.hasOwnProperty('erro')) {
                        this.httpError(resposta.erro);
                    }
                }, response => {
                    console.log('não entrou');
                    this.$message.error('Oops, o servidor não respondeu :(');
                });
            },
            verificarNivel(nivel) {
                this.$store.dispatch('setNivel', nivel);
                switch (nivel) {
                    case 0:
                        this.errorLogin();
                        break;
                    case 3:
                        console.log("ACESSO COMO CANTINA");
                        break;
                    case 4:
                        this.$router.push('/home');
                        break;
                }

            },
            errorLogin() {
                this.loginErrorMsg = true;
                // continuar função
            },
            httpError(erro) {
                switch (erro) {
                    case '500':
                        alert('Desculpe, estamos com problemas em nossos servidores');
                        break;
                }
            },
            saveEmail() {
                sessionStorage.setItem('email', this.email);
            }
        },
        mounted() {
            const email = document.getElementsByName('email')[0];
            const pass = document.getElementsByName('password')[0];
            email.addEventListener('keyup', (e) => {
                if (e.key === 'Enter') {
                    pass.focus();
                }
            });

            pass.addEventListener('keyup', (e) => {
                if (e.key === 'Enter') {
                    this.tryConnection(this.loginURL);
                }
            });

            this.resizeWindow();
            this.Sizes();
            if (sessionStorage.hasOwnProperty('email')) {
                this.email = sessionStorage.getItem('email');
            }
        },
        watch: {
            windowWidth() {
                this.Sizes();
            }
        }
    }
</script>
<style>
    template {
        margin: 0;
        padding: 0;
    }
    
    div#log {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: 10%;
    }
    
    div#form {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    
    div.content {
        width: 450px;
    }
    
    div#back {
        background-color: #806539;
        padding: 0 0.5rem;
        border-radius: 25px;
    }
    
    .first-green {
        color: #30dd65;
    }
    
    label.el-form-item__label {
        color: #30dd65;
    }
    
    div.content#buttons {
        display: flex;
        justify-content: space-between;
    }
    
    div#login-text {
        text-align: center;
        margin: 0;
        padding: 0;
    }
    
    a {
        color: white;
    }
</style>


