<template>
    <div class="menubar">
        <el-row>
            <el-col :span="16" class="buttons" id="lanche"><a class="buttons-link" @click="clicado('lanche')"><i class="fa fa-coffee fa-2x"></i>LANCHE</a></el-col>
        </el-row>
        <el-row>
            <el-col :span="16" class="buttons" @click="" id="almoco"><a class="buttons-link" @click="clicado('almoco')"><i class="fa fa-cutlery fa-2x" aria-hidden="true"></i>ALMOÇO</a></el-col>
        </el-row>
        <el-row>
            <el-col :span="16" class="buttons" @click="" id="config"><a class="buttons-link" @click="clicado('config')"><i class="fa fa-cog fa-2x" aria-hidden="true"></i>CONFIG</a></el-col>
        </el-row>
        <el-row>
             <el-col :span="16" class="buttons" id="sair"><a class="buttons-link" @click="clicado('logout')"><i class="fa fa-sign-out fa-2x" aria-hidden="true"></i>SAIR</a></el-col>
        </el-row>
    </div>
</template>
<script>
    export default {
        data() {
            return {}
        },
        methods: {
            clicado() {

            }
        },
        mounted() {
            let botoes = document.getElementsByClassName('buttons');
            for (let botao of botoes) {
                botao.addEventListener('mouseover', (el) => {
                    //console.log(el.target);
                    botao.classList.add('el-col-24');
                });
                botao.addEventListener('mouseleave', (el) => {
                    botao.classList.remove('el-col-24');
                })
            }
            //console.log(botoes);
            /*botoes.addEventListener('mouseover', (e) => {
                console.log(e);
            });*/
        }
    }
</script>
<style scoped>
    .menubar {
        z-index: 9;
        width: 15vw;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: fixed;
    }
    
    .buttons {
        text-align: center;
    }
    
    .buttons:hover {
        cursor: pointer;
    }
    
    .buttons-link {
        display: flex;
        flex-direction: column;
        padding: 2rem 0;
        font-size: 0.75rem;
        background-color: #806539;
    }
    
    .buttons:hover>.buttons-link {
        background-color: #705530;
        color: #30dd65;
    }
    
    .ativo a {
        color: #30dd65;
        background-color: #806539;
    }
</style>


