<template>
    <div>
        <header>
            <el-row>
                <el-col class="logo">CANTINA DA FAMÍLIA</el-col>
            </el-row>
            <el-row>
                <router-link to="/login"><el-col :span="12" class="menu" id="login">LOGIN</el-col></router-link>
                <router-link to="/"><el-col :span="12" class="menu">CONTATO</el-col></router-link>
            </el-row>
        </header>
    </div>
</template>
<script>
    export default {
        data() {
            return {

            }
        },
        methods: {
            botao(op) {
                this.$emit('botao', op);
            }
        }
    }
</script>
<style scoped>
    .menu:hover a {
        color: white;
    }
    
    .menu {
        border: 1px solid red;
        text-align: center;
        font-family: 'Open Sans', sans-serif;
        padding: 0.75rem 0;
        transition: background 0.5s, color 0.5s;
    }
    
    .logo {
        border: 1px solid blue;
        text-align: center;
        padding: 1.25rem 0;
    }
    
    .menu:hover {
        background-color: red;
        color: white;
        cursor: pointer;
    }
    
    a {
        color: black;
    }
</style>


