<template>
    <div class="bar">
        <el-row>
            <el-col :span="6" class="buttons"><a  @click="clicado('lanche')"><i class="fa fa-coffee fa-2x"></i>LANCHE</a></el-col>
            <el-col :span="6" class="buttons" @click=""><a @click="clicado('almoco')"><i class="fa fa-cutlery fa-2x" aria-hidden="true"></i>ALMOÇO</a></el-col>
            <el-col :span="6" class="buttons" @click=""><a @click="clicado('config')"><i class="fa fa-cog fa-2x" aria-hidden="true"></i>CONFIG</a></el-col>
            <el-col :span="6" class="buttons" id="sair"><a @click="clicado('logout')"><i class="fa fa-sign-out fa-2x" aria-hidden="true"></i>SAIR</a></el-col>
        </el-row>
    </div>
</template>
<script>
    export default {
        data() {
            return {

            };
        },
        methods: {
            clicado(btn) {
                console.log('fui chamado');

                switch (btn) {
                    case 'logout':
                        console.log('cliquei para sair');
                        this.$emit('botao', btn);
                        break;
                }
            },
            seila() {
                console.log('sei la');
            }
        }
    }
</script>
<style scoped>
    .bar {
        position: fixed;
        width: 100%;
        bottom: 0;
        z-index: 9;
    }
    
    .buttons {
        text-align: center;
    }
    
    .buttons:hover {
        cursor: pointer;
    }
    
    .buttons>a {
        display: flex;
        flex-direction: column;
        padding: 0.5rem;
        font-size: 0.75rem;
        background-color: #806539;
    }
    
    .buttons:hover>a {
        background-color: #705530;
        color: #30dd65;
    }
    
    .ativo {
        color: #30dd65;
    }
</style>


