import Vue from 'vue';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-default/index.css';
import App from './App.vue';

import VueRouter from 'vue-router';
import VueResource from 'vue-resource';
import router from './router/index.js';
import Vuex from 'vuex';

import locale from 'element-ui/lib/locale/lang/pt-br';

Vue.use(VueResource);
Vue.use(ElementUI, { locale });
Vue.use(Vuex);

Vue.mixin({
    data() {
        return {
            windowWidth: 0
        }
    },
    created() {
        window.addEventListener('resize', this.resizeWindow);
        window.addEventListener('load', this.resizeWindow);
    },
    methods: {
        resizeWindow() {
            this.windowWidth = window.innerWidth;
            return this.windowWidth;
        }
    }
});

const store = new Vuex.Store({
    state: {
        nivel: 0
    },
    mutations: {
        setNivel(state, nivel) {
            state.nivel = nivel;
            console.log('setNivel = ' + state.nivel);
        }
    },
    getters: {
        getNivel: (state) => {
            return state.nivel;
        }
    },
    actions: {
        setNivel({ commit }, nivel) {
            commit('setNivel', nivel);
        }
    }
});


new Vue({
    router,
    store,
    el: '#app',
    http: {
        emulateJSON: true,
        emulateHTTP: true,
        headers: {
            'X-CSRF-TOKEN': document.querySelector('#token').getAttribute('content')
        }
    },
    render: h => h(App)
});


