<template>
    <div class="home-bg">
        <component :is="screen" @botao="logout"></component>
        <component :is="space">
           <component :is="opcao"></component>
        </component>
        
        <!--bar-component v-if="screen" @botao='logout' ></bar-component-->
    </div>
    
</template>
<script>
    import Bar from './Bar.vue';
    import FullBar from './FullBar.vue';
    import Intro from './Intro.vue';
    import FullIntro from './FullIntro.vue';
    import PaiLanches from './pais/Lanches.vue';

    export default {
        components: {
            BarComponent: Bar,
            FullBarComponent: FullBar,
            IntroComponent: Intro,
            FullIntroComponent: FullIntro
        },
        data() {
            return {
                op: '',
                screen: Bar,
                space: Intro,
                opcao: PaiLanches
            }
        },
        created() {
            this.op = this.$store.getters.getNivel;
        },
        methods: {
            logout() {
                console.log('to no pai home');
                this.$confirm('Deseja sair?', 'Aviso', {
                    confirmButtonText: 'Sim',
                    cancelButtonText: 'Não',
                    type: 'info'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: 'Logout realizado'
                    });
                    this.$store.dispatch('setNivel', 0);
                    this.$router.push('/');
                }).catch(() => {

                });
            },
            fixSize() {
                if (this.windowWidth > 1024) {
                    this.screen = FullBar;
                    this.space = FullIntro;
                } else {
                    this.screen = Bar;
                    this.space = Intro;
                }
            }
        },
        mounted() {
            this.resizeWindow();
            this.fixSize();
        },
        watch: {
            windowWidth() {
                this.fixSize();
            }
        }
    }
</script>
<style>
    * {
        margin: 0;
        padding: 0;
    }
    
    .home-bg {
        height: 100vh;
        background-color: #30dd65;
    }
    
    .opcoes {
        width: 300px;
        height: 300px;
    }
    
    .content-1 {
        background-color: blue;
        margin: 5vh 4vw;
        border-radius: 25px;
        padding: 1rem;
        display: inline-block;
    }
</style>


