<template>
    <div class="lanche">
        <h1>LANCHES</h1>
        <br>
        <br>
        <el-form ref="form" label-position="top" @submit.prevent="">
            <el-form-item label="DATA">
                <el-date-picker 
                    v-model="date" 
                    type="date" 
                    placeholder="Escolha uma data" id="date"
                    format="dd/MM/yyyy">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="FILHO">
                <el-select v-model="filho" placeholder="Selecione">
                    <el-option v-for="f in filhos" :label="f.label" :value="f.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="LANCHE">
                <el-select v-model="lanche" placeholder="Selecione">
                    <el-option v-for="l in lanches" :label="l.label" :value="l.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="QUANTIDADE">
                <el-input-number v-model="qtde" :min="1"></el-input-number>
            </el-form-item>
        </el-form>
        <div class="bottom">
            <div id="lista">
                <i class="fa fa-list-alt fa-3x"></i>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                date: '',
                filhos: [],
                filho: '',
                lanches: [],
                lanche: '',
                qtde: 1
            }
        }
    }
</script>
<style scoped>
    .lanche {
        background-color: #806539;
        display: flex;
        flex-direction: column;
        align-content: center;
        margin-top: 1rem;
        margin-left: 30vw;
        position: absolute;
    }
    
    input[type=date]::-webkit-inner-spin-button {
        -webkit-appearance: none;
        display: none;
    }
    
    input[type=date]::-webkit-calendar-picker-indicator {
        -webkit-appearance: none;
    }
</style>


