<template>
    <div>
        <component :is="menu">
        </component>
                
    </div>
</template>
<script>
    import Menu1 from './Menu.vue';
    import Menu2 from './Menu2.vue';
    import Login from './Login.vue';

    export default {
        data() {
            return {
                menu: Menu2
            }
        },
        components: {
            Menu1,
            Menu2,
            Login
        }
    }
</script>
<style scoped></style>


